<template>
    <div id="inventaire">
        <!-- les liens s'adapteront à l'utilisateur connecté -->
        <nav class="navbar navbar-expand-md navbar-light">
            <span class="navbar-brand mb-0 h1">Gestion des fournitures scolaires</span>
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent"
                aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>

            <div class="collapse navbar-collapse" id="navbarSupportedContent">
                <ul class="navbar-nav mr-auto">
                    <li class="nav-item">
                        <a class="nav-link" href="accueil">Accueil</a>
                    </li>
                    <li class="nav-item dropdown active">
                        <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true"
                            aria-expanded="false">
                            Consulter
                        </a>
                        <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                            <a class="dropdown-item" href="inv-ele">Inventaire d'un élève</a>
                            <a class="dropdown-item" href="inv-ens">Inventaire d'un enseignant</a>
                        </div>
                    </li>
                    <li class="nav-item dropdown">
                        <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true"
                            aria-expanded="false">
                            Distribuer
                        </a>
                        <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                            <a class="dropdown-item" href="distr-cla">A une classe</a>
                            <a class="dropdown-item" href="distr-ele">A un élève</a>
                            <a class="dropdown-item" href="distr-ens">A un enseignant</a>
                        </div>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" href="gerer-fourniture">Gérer stock de fourniture</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" href="gerer-categorie">Gérer les catégories</a>
                    </li>
                </ul>
                <p class="utilisateur">Nom de l'utilisateur connecté</p>                
                <a href="form_conn" class="btn btn-danger" role="button">Déconnexion</a>
            </div>
        </nav>

        <div class="container-fluid col-md-11">

            <h1>Inventaire</h1>


            <form action="#" class="row justify-content-md-center top-buffer">




                <div class="col-md-12 row justify-content-md-center align-items-center top-buffer">
                    <div class="col-md-5">
                        <p>Quantité actuelle de <span class="fourniture">{{inventaire[3]['designation']}}</span></p>
                    </div>
                    <div class="col-md-3 ">
                        <p><span class="fourniture">{{inventaire[3]['quantite']}}</span></p>
                    </div>
                </div>

                <div class="col-md-12 row justify-content-md-center align-items-center top-buffer">
                    <div class="col-md-5">
                        <label for="designation">Nouvelle quantité :</label>
                    </div>
                    <div class="col-md-3 ">
                        <input type="text" id="designation" class="form-control">
                    </div>
                </div>

                <div class="col-md-12 row justify-content-md-center top-buffer">
                    <div class="col-md-2">
                        <a href="inv-ens" class="btn btn-primary">Annuler</a>
                    </div>
                    <div class="col-md-2">
                        <button type="submit" class="btn btn-primary">Modifier</button>
                    </div>
                </div>

            </form>

        </div>
    </div>
</template>

<script>
import axios from "axios";
export default {
  name: "inventaire",
  data() {
    return {
      inventaire: [],
      prixTotal: 0,
      errors: []
    };
  },
  methods: {
    calculTotal: function(inventaire_) {
      let total = 0;
      inventaire_.forEach(element => {
        total += element["prix"] * element["quantite"];
      });
      this.prixTotal = total;
    }
  },
  // Fetches posts when the component is created.
  created() {
    axios
      .get(`http://localhost:3000/inventaire`)
      .then(response => {
        // JSON responses are automatically parsed.
        this.inventaire = response.data;
        this.calculTotal(this.inventaire);
      })
      .catch(e => {
        this.errors.push(e);
      });
  }
};
</script>
<!-- styling for the component -->
<style scoped>
.max-600 {
  max-width: 600px;
}

label {
  text-align: left;
}


#tdTotal {
    text-align: right;
}

form {
    max-width: 600px;
    margin: auto;
}

.top-buffer { 
    margin-top: 20px;
}

.fourniture {
    font-weight: bold;
}
</style>


